import {
    Component,
    Host,
    Prop,
    h,
    Element,
    EventEmitter,
    Event,
} from '@stencil/core';
import { LoadingType } from '../atui-loading/atui-loading';
import { cva } from 'class-variance-authority';
import { AtuiEvent } from '../../types/events';

export type ButtonType =
    | 'primary'
    | 'primaryOutline'
    | 'primaryText'
    | 'secondary'
    | 'secondaryOutline'
    | 'secondaryText'
    | 'destructive'
    | 'destructiveOutline'
    | 'destructiveText';

export type ButtonSize = 'sm' | 'md' | 'lg';

const variants = cva(
    'group text-button relative box-border flex inline-flex cursor-pointer items-center justify-center gap-4 overflow-hidden rounded-[0.3rem] font-medium whitespace-nowrap capitalize duration-150 ease-in-out outline-none focus:outline-none',
    {
        variants: {
            disabled: {
                false: null,
                true: 'pointer-events-none opacity-30 grayscale-[1]',
            },
            size: {
                sm: 'py-4',
                md: 'py-[6px]',
                lg: 'py-8',
            },
            type: {
                primary:
                    'bg-active-foreground text-impact focus-visible:ring-active-foreground/30 group-hover:bg-active-foreground/90 group-focus:bg-active-foreground/80 focus-visible:ring-2',
                primaryOutline:
                    'border-primary-foreground text-active-foreground focus-visible:ring-active-foreground/30 group-hover:bg-active-foreground/10 group-focus:bg-active-foreground/20 border border-solid bg-transparent focus-visible:ring-2',
                primaryText:
                    'text-active-foreground focus-visible:ring-active-foreground/30 group-hover:bg-active-foreground/10 group-focus:bg-active-foreground/20 bg-transparent focus-visible:ring-2',
                secondary:
                    'text-impact focus-visible:ring-active-foreground/30 bg-slate-600 group-hover:bg-slate-700 group-focus:bg-slate-800 focus-visible:ring-2',
                secondaryOutline:
                    'border-dark text-foreground focus-visible:ring-active-foreground/30 border border-solid bg-transparent group-hover:bg-slate-100 group-focus:bg-slate-200 focus-visible:ring-2',
                secondaryText:
                    'text-foreground focus-visible:ring-active-foreground/30 bg-transparent group-hover:bg-slate-100 group-focus:bg-slate-200 focus-visible:ring-2',
                destructive:
                    'bg-destructive-foreground text-impact focus-visible:ring-active-foreground/30 group-hover:bg-destructive-foreground/90 group-focus:bg-destructive-foreground/80 focus-visible:ring-2',
                destructiveOutline:
                    'border-destructive-foreground text-destructive-foreground focus-visible:ring-destructive-foreground/30 group-hover:bg-destructive-foreground/10 group-focus:bg-destructive-foreground/20 border border-solid bg-transparent focus-visible:ring-2',
                destructiveText:
                    'text-destructive-foreground focus-visible:ring-destructive-foreground/30 group-hover:bg-destructive-foreground/10 group-focus:bg-destructive-foreground/20 bg-transparent focus-visible:ring-2',
            },
            hasLabel: {
                true: null,
                false: null,
            },
        },
        compoundVariants: [
            {
                size: 'sm',
                hasLabel: false,
                class: 'px-4',
            },
            {
                size: 'sm',
                hasLabel: true,
                class: 'px-8',
            },
            {
                size: 'md',
                hasLabel: false,
                class: 'px-[6px]',
            },
            {
                size: 'md',
                hasLabel: true,
                class: 'px-8',
            },
            {
                size: 'lg',
                hasLabel: false,
                class: 'px-8',
            },
            {
                size: 'lg',
                hasLabel: true,
                class: 'px-[12px]',
            },
        ],
        defaultVariants: {
            size: 'lg',
            type: 'primary',
            hasLabel: true,
            disabled: false,
        },
    },
);

const focusIndicatorVariants = cva(
    'pointer-events-none absolute top-0 left-0 z-10 h-full w-full transition-colors duration-300 ease-in-out',
    {
        variants: {
            type: {
                primary:
                    'group-hover:bg-blue-900/30 group-active:bg-blue-900/70',
                primaryOutline:
                    'group-hover:bg-active-foreground/10 group-active:bg-active-foreground/20',
                primaryText:
                    'group-hover:bg-active-foreground/10 group-active:bg-active-foreground/20',
                secondary:
                    'group-hover:bg-slate-900/10 group-active:bg-slate-900/20',
                secondaryOutline:
                    'group-hover:bg-slate-500/10 group-active:bg-slate-500/20',
                secondaryText:
                    'group-hover:bg-slate-500/10 group-active:bg-slate-500/20',
                destructive:
                    'group-hover:bg-red-900/30 group-active:bg-red-900/70',
                destructiveOutline:
                    'group-hover:bg-destructive-foreground/10 group-active:bg-destructive-foreground/20',
                destructiveText:
                    'group-hover:bg-destructive-foreground/10 group-active:bg-destructive-foreground/20',
            },
        },
    },
);

const spinnerColourPerType = {
    primary: 'default' as LoadingType,
    primaryOutline: 'secondary' as LoadingType,
    primaryText: 'secondary' as LoadingType,
    secondary: 'secondary' as LoadingType,
    secondaryOutline: 'secondary' as LoadingType,
    secondaryText: 'secondary' as LoadingType,
    destructive: 'default' as LoadingType,
    destructiveOutline: 'error' as LoadingType,
    destructiveText: 'error' as LoadingType,
};

/**
 * @category Actions
 * @description A versatile button component for user interactions with multiple styling variants, sizes, and loading states. Supports icons, custom content through slots, and accessibility features.
 *
 * @slot - Placed after the label, but before the icon_right. Can be used as a replacement for the label prop.
 */
@Component({
    tag: 'atui-button',
    shadow: false,
})
export class AtuiButtonComponent {
    /**
     * If set, will request submit from the closest form element when clicked
     */
    @Prop()
    submit: boolean = false;

    /**
     * Styling of the button
     */
    @Prop()
    type: ButtonType = 'primary';

    /**
     * Size of the button
     */
    @Prop()
    size: ButtonSize = 'lg';

    /**
     * Label to be displayed within the button
     */
    @Prop()
    label: string;

    /**
     * When set the button's styling will change and will no longer be interactive
     */
    @Prop()
    disabled: boolean = false;

    /**
     * Material icon to be displayed before the label within the button
     */
    @Prop()
    icon?: string;

    /**
     * Material icon to be displayed after the label within the button
     */
    @Prop()
    icon_after?: string;

    /**
     * When set, will display a loading spinner inside the button and hide all labels & icons
     */
    @Prop()
    in_progress = false;

    /**
     * Delay period on spinner
     */
    @Prop()
    spinner_delay_ms = 1000;

    @Element()
    el: HTMLAtuiButtonElement;

    /**
     * Emits when the button is clicked
     */
    @Event()
    atuiClick: EventEmitter<AtuiEvent>;

    canHideSpinner = true;

    get spinnerColour(): LoadingType {
        return spinnerColourPerType[this.type];
    }

    private hideSpinnerIfAfterMinimumDisplayPeriod(): void {
        if (this.in_progress && this.canHideSpinner) {
            this.in_progress = false;
        }
    }

    private setSpinner(): void {
        if (this.in_progress) {
            this.canHideSpinner = false;
            setTimeout(() => {
                this.canHideSpinner = true;
                this.hideSpinnerIfAfterMinimumDisplayPeriod();
            }, this.spinner_delay_ms);
        } else {
            this.hideSpinnerIfAfterMinimumDisplayPeriod();
        }
    }

    handleClick(event: Event): void {
        this.atuiClick.emit({
            originalEvent: event,
            componentType: 'atui-button',
        });
        if (this.submit) {
            this.el.closest('form').requestSubmit();
        }
    }

    handleKeyDown(event: KeyboardEvent): void {
        if (event.key === ' ' || event.key === 'Enter') {
            event.preventDefault();
            this.atuiClick.emit({
                originalEvent: event,
                componentType: 'atui-button',
                element: this.el,
            });
            if (this.submit) {
                this.el.closest('form').requestSubmit();
            }
        }
    }

    componentWillUpdate(): void {
        this.setSpinner();
    }

    render() {
        const classname = variants({
            disabled: this.disabled,
            size: this.size,
            type: this.type,
            hasLabel: !!this.label,
        });
        const focusIndicatorClassname = focusIndicatorVariants({
            type: this.type,
        });

        return (
            <Host
                class={classname}
                role="button"
                tabIndex={0}
                onKeyDown={(event) => this.handleKeyDown(event)}
                onClick={(event) => this.handleClick(event)}
            >
                <div class="z-20 flex h-full w-full items-center justify-center gap-4">
                    {this.in_progress && (
                        <atui-loading
                            class="absolute"
                            size="sm"
                            type={this.spinnerColour}
                        />
                    )}

                    <slot name="icon" />
                    {this.icon && (
                        <span
                            class={`material-icons h-16 w-16 text-[16px] leading-[16px] ${this.in_progress ? 'invisible' : 'visible'}`}
                            data-name="button-icon"
                        >
                            {this.icon}
                        </span>
                    )}

                    {this.label && (
                        <span
                            class={`leading-[16px] ${this.in_progress ? 'invisible' : 'visible'}`}
                            data-name="button-label"
                        >
                            {this.label}
                        </span>
                    )}
                    <slot />

                    <slot name="icon_after" />
                    {this.icon_after && (
                        <span
                            class={`material-icons h-16 w-16 text-[16px] leading-[16px] ${this.in_progress ? 'invisible w-0' : 'visible'}`}
                            data-name="button-icon-right"
                        >
                            {this.icon_after}
                        </span>
                    )}
                </div>

                <div
                    data-name="focus-indicator"
                    role="presentation"
                    class={focusIndicatorClassname}
                ></div>
            </Host>
        );
    }
}
