import { Component, h, Prop, Host } from '@stencil/core';

export type LoadingVariant = 'spinner' | 'dots' | 'typing' | 'wave';
export type LoadingType = 'default' | 'error' | 'secondary';
export type LoadingSize = 'sm' | 'md' | 'lg';

const loadingVariants = {
    type: {
        default: 'text-primary',
        error: 'text-destructive-foreground',
        secondary: 'text-primary-foreground',
    },
    size: {
        sm: 'text-sm',
        md: 'text-base',
        lg: 'text-lg',
    },
    spinnerSize: {
        sm: 'h-16 w-16 border',
        md: 'h-32 w-32 border-2',
        lg: 'h-64 w-64 border-4',
    },
};

/**
 * @category Feedback
 * @description A versatile loading component with multiple animation types including spinner, dots, typing, wave, and thinking indicators. Perfect for indicating ongoing processes or data fetching states across different contexts.
 */
@Component({
    tag: 'atui-loading',
    styleUrl: 'atui-loading.scss',
    shadow: false,
})
export class AtuiLoadingComponent {
    /**
     * Type of loading animation to display
     */
    @Prop()
    variant: LoadingVariant = 'spinner';

    /**
     * Visual type/color of the loading indicator
     */
    @Prop()
    type: LoadingType = 'default';

    /**
     * Size of the loading indicator
     */
    @Prop()
    size: LoadingSize = 'md';

    /**
     * Custom text to display with text-based animations
     */
    @Prop()
    text: string = 'Loading';

    get typeClasses() {
        return loadingVariants.type[this.type];
    }

    get sizeClasses() {
        return loadingVariants.size[this.size];
    }

    get spinnerSizeClasses() {
        return loadingVariants.spinnerSize[this.size];
    }

    private renderSpinner() {
        const borderColor =
            this.type === 'default'
                ? 'border-primary'
                : this.type === 'error'
                  ? 'border-destructive-foreground'
                  : 'border-primary-foreground';

        return (
            <Host
                class={`${borderColor} ${this.spinnerSizeClasses} inline-block animate-spin rounded-full border-solid border-e-transparent align-[-0.125em] motion-reduce:animate-[spin_1.5s_linear_infinite]`}
                role="status"
                aria-label="Loading"
            />
        );
    }

    private renderTyping() {
        return (
            <Host
                class={`flex items-center gap-4`}
                role="status"
                aria-label="Typing"
            >
                <span
                    class="inline-block h-4 w-4 animate-[typing_1s_infinite] rounded-full bg-gray-500"
                    style={{ animationDelay: '0ms' }}
                ></span>
                <span
                    class="inline-block h-4 w-4 animate-[typing_1s_infinite] rounded-full bg-gray-500"
                    style={{ animationDelay: '250ms' }}
                ></span>
                <span
                    class="inline-block h-4 w-4 animate-[typing_1s_infinite] rounded-full bg-gray-500"
                    style={{ animationDelay: '500ms' }}
                ></span>
            </Host>
        );
    }

    render() {
        switch (this.variant) {
            case 'typing':
                return this.renderTyping();
            case 'spinner':
            default:
                return this.renderSpinner();
        }
    }
}
